.header-head {
  max-width: 100%;
  height: 90vh;
  position: relative;
  background: var(--dark-blue-color);
  background-image: linear-gradient(rgba(34, 33, 44, 0.822), rgba(34, 33, 44, 0.925)),
    url(./code.jpg);
  background-position: center;
  background-size: cover;
  display: grid;
  grid-template-rows: 1fr 2fr 1fr;
  justify-content: center;
  align-items: center;
  justify-items: center;
}

.header-container {
  grid-row: 2;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
}
.header-container-icon {
  color: var(--blue-color);
  font-size: 200px;
}
.header-container-text {
  width: 400px;
  font-family: var(--roboto-font);
  padding-left: 20px;
}
.header-container-text-p {
  color: Var(--white-color);
  font-weight: 300;
  margin-left: 10px;
}
.header-container-html-text {
  color: var(--blue-color);
}

.msg-role-para-baixo {
  grid-row: 3;
  justify-self: end;
  color: var(--pink-color);
  font-weight: 400;
}
.cursor {
  background: lime;
  line-height: 17px;
  margin-left: 3px;
  animation: blink 0.8s infinite;
  width: 4px;
  height: 18px;
  display: inline-block;
}

@keyframes blink {
  0% {
    background: var(--pink-color);
  }
  50% {
    background: transparent;
  }
  100% {
    background: var(--pink-color);
  }
}

@media only screen and (max-width: 725px) {
  .header-head {
    max-width: 100%;
    height: 70vh !important;
    display: grid;
  }

  .header-container {
    max-width: 100vh;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    padding: 0px;
  }
  .header-container-text {
    width: 250px;
    font-family: var(--roboto-font);
    padding-left: 0px;
  }
  .header-container-icon {
    font-size: 110px;
    padding-bottom: 5px;
  }
  .header-container-html-text {
    display: none;
  }
  .header-container-text-p {
    width: auto;
    margin: 0px;
    text-align: center;
  }

  .msg-role-para-baixo {
    font-size: 14px;
  }
}
