@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  padding: 0;
}


:root {
  --pink-color: #ff79c6;
  --dark-blue-color: #22212c;
  --blue-color: #36fcff;
  --white-color: #fdfdfd;
  --green-color: #83f59e;
  --footer-color: #131215;

  --roboto-font: "Roboto", sans-serif;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

.default-section {
  font-family: "Roboto", sans-serif;
  font-family: var(--roboto-font);
  max-width: 100%;
  height: -webkit-min-content;
  height: min-content;
  padding: 100px 0px 0px 0px;
  background-color: #22212c;
  background-color: var(--dark-blue-color);
  justify-content: center;
  align-items: center;
}
.default-selection-h1 {
  margin: 0px;
  padding: 30px 0px 20px 0px;
  text-align: center;
  font-weight: 700;
  font-size: 35px;
  color: #ff79c6;
  color: var(--pink-color);
}

.menu-nav {
  max-width: 100%;
  background-color: var(--dark-blue-color);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 70px 0px 70px;
  font-family: var(--roboto-font);
}

.menu-h3 {
  color: var(--pink-color);
  font-weight: 700;
}

.menu-ul {
  min-width: -webkit-min-content;
  min-width: min-content;
  display: flex;
  flex-direction: row;
  color: var(--white-color);
  list-style-type: none;
  font-size: 0.9em;
  font-weight: 300;
}

.menu-li {
  padding: 10px;
  color: var(--white-color);
  font-weight: 400;
}
.menu-li:hover {
  color: var(--pink-color) !important;
}

@media only screen and (max-width: 480px) {
  .menu-nav {
    flex-direction: column;
    justify-content: center;
    padding: 10px 0px 10px 0px;
  }
  .menu-h1 {
    font-size: 1.5em;
    margin: 0px;
  }
  .menu-ul {
    font-size: 1em;
    flex-shrink: 200px;
    flex-wrap: nowrap;
    padding: 2px;
    margin: 0px;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.header-head {
  max-width: 100%;
  height: 90vh;
  position: relative;
  background: var(--dark-blue-color);
  background-image: linear-gradient(rgba(34, 33, 44, 0.822), rgba(34, 33, 44, 0.925)),
    url(/Personal-website/static/media/code.e24bf3cb.jpg);
  background-position: center;
  background-size: cover;
  display: grid;
  grid-template-rows: 1fr 2fr 1fr;
  justify-content: center;
  align-items: center;
  justify-items: center;
}

.header-container {
  grid-row: 2;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
}
.header-container-icon {
  color: var(--blue-color);
  font-size: 200px;
}
.header-container-text {
  width: 400px;
  font-family: var(--roboto-font);
  padding-left: 20px;
}
.header-container-text-p {
  color: Var(--white-color);
  font-weight: 300;
  margin-left: 10px;
}
.header-container-html-text {
  color: var(--blue-color);
}

.msg-role-para-baixo {
  grid-row: 3;
  justify-self: end;
  color: var(--pink-color);
  font-weight: 400;
}
.cursor {
  background: lime;
  line-height: 17px;
  margin-left: 3px;
  animation: blink 0.8s infinite;
  width: 4px;
  height: 18px;
  display: inline-block;
}

@keyframes blink {
  0% {
    background: var(--pink-color);
  }
  50% {
    background: transparent;
  }
  100% {
    background: var(--pink-color);
  }
}

@media only screen and (max-width: 725px) {
  .header-head {
    max-width: 100%;
    height: 70vh !important;
    display: grid;
  }

  .header-container {
    max-width: 100vh;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    padding: 0px;
  }
  .header-container-text {
    width: 250px;
    font-family: var(--roboto-font);
    padding-left: 0px;
  }
  .header-container-icon {
    font-size: 110px;
    padding-bottom: 5px;
  }
  .header-container-html-text {
    display: none;
  }
  .header-container-text-p {
    width: auto;
    margin: 0px;
    text-align: center;
  }

  .msg-role-para-baixo {
    font-size: 14px;
  }
}

.sobre-section {
  display: flex;
}

.sobre-container {
  max-width: 400px;
  display: flex;
  flex-direction: column;
  font-family: var(--roboto-font);
  align-items: center;
}
.sobre-profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.sobre-h1 {
  color: var(--blue-color);
  font-weight: 700;
  letter-spacing: 0.2em;
  margin: 20px 0px 10px 0px;
}
.sobre-p {
  color: var(--white-color);
  text-align: center;
  font-weight: 300;
  margin: 0px;
}
.sobre-icons-container {
  display: flex;
  flex-direction: row;
  margin-top: 5px;
}
.sobre-icons {
  font-size: 25px;
  padding: 5px;
  color: var(--white-color);
}

@media only screen and (max-width: 480px) {
  .sobre-container {
    max-width: 300px;
  }
  .sobre-profile-image {
    width: 150px;
    height: 150px;
  }
  .sobre-icons {
    font-size: 33px;
    margin: 5px;
  }
}

.list-ul {
  grid-row: 2;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style-type: none;
  justify-content: center;
  margin: 0px;
  padding: 0px;
}
.list-li {
  width: 200px;
  height: 200px;
  margin: 10px 20px 10px 20px;
  border: solid;
  border-width: 2px;
  border-color: var(--blue-color);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  transition-duration: 700ms;
}
.list-li:hover {
  border-color: var(--pink-color);
}
.list-h2 {
  color: var(--pink-color);
  align-self: center;
}
.list-dateFork {
  display: flex;
  flex-direction: row;
  color: var(--white-color);
  font-size: 14px;
  justify-content: space-between;
  align-items: center;
}
.list-clock-date {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.list-clock-icon {
  margin-right: 6px;
  align-self: center;
}
.list-p {
  color: var(--white-color);
  text-align: justify;
}

.errorMessage-div {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  text-align: center;
}
.errorMessage-icon {
  color: var(--pink-color);
  font-size: 40px;
  margin: 10px;
  align-self: center;
}
.errorMessage-Text {
  font-size: 20px;
  margin: 10px;
  color: var(--white-color);
}

.tecnologias-section {
  font-family: var(--roboto-font);
  max-width: 100%;
  background-color: var(--dark-blue-color);
  display: grid;
  grid-template-rows: 1fr 1fr;
  justify-content: center;
  align-items: center;
  align-self: baseline;
  margin-top: 10px;
}
.tecnologias-h2 {
  grid-row: 1;
  margin: 0px;
  padding: 30px 0px 20px 0px;
  text-align: center;
  font-weight: 700;
  font-size: 20px;
  color: var(--blue-color);
}
.tecnologias-container {
  grid-row: 2;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
}
.tecnologias-container-icons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-items: center;
  align-items: center;
  align-content: center;
  justify-content: center;
}
.tecnologias-icons {
  color: var(--white-color);
  margin: 10px;
  font-size: 40px;
}

.portfolio-section {
  display: grid;
  grid-template-rows: 1fr 2fr auto;
  flex-direction: column;
}
.portfolio-h1 {
  grid-row: 1;
}

@media only screen and (max-width: 600px) {
  .portfolio-section {
    display: flex;
    flex-direction: column;
  }
}

.contato-section {
  display: grid;
  grid-template-rows: 1fr 2fr;
  flex-direction: column;
  padding-bottom: 100px;
}
.contato-h1 {
  grid-row: 1;
}
.contato-form {
  grid-row: 2;
  width: 500px;
  display: flex;
  flex-direction: column;
  align-self: baseline;
}
.contato-label {
  color: var(--white-color);
  margin-bottom: 2px;
}
.contato-input {
  margin: 0px 0px 15px 0px;
  height: 25px;
  border-radius: 7px;
  border: solid;
  border-color: var(--blue-color);
}
.contato-input-text {
  height: 200px;
}
.contato-submit {
  width: 100px;
  background: var(--dark-blue-color);
  color: var(--white-color);
  padding: 5px;
  border: solid;
  border-color: var(--pink-color);
  border-radius: 7px;
  border-width: 2px;
  align-self: flex-end;
  transition: 700ms;
}
input[type="submit"]:hover {
  border-color: var(--blue-color);
}
input[type="submit"]:active {
  border-color: var(--green-color);
}

@media only screen and (max-width: 550px) {
  .contato-form {
    width: 100%;
  }
  .contato-input {
    width: auto;
  }
}

.backToTop-div {
  width: 50px;
  height: 50px;
  bottom: 20px;
  right: 20px;
  border-radius: 100px;
  background-color: var(--pink-color);
  color: var(--dark-blue-color);
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 500ms;
}
.backToTop-div:hover {
  width: 55px;
  height: 55px;
  color: var(--white-color);
}

.footer-section {
  font-family: var(--roboto-font);
  font-weight: 400;
  max-width: 100%;
  height: 80px;
  background-color: var(--footer-color);
  color: var(--pink-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px 0px 30px;
}

